import { Component, OnInit } from '@angular/core';
import * as $ from 'jquery';
import { HttpService } from '../http.service';
@Component({
  selector: 'app-setting',
  templateUrl: './setting.component.html',
  styleUrls: ['./setting.component.css']
})
export class SettingComponent implements OnInit {
  lock = false;
  lock1 = false;
  temp = true;

  constructor(private _httpService:HttpService) { }

  ngOnInit() {
    $(document).ready(function(){
      $('.boxContainer').click(function(){
        this._httpService.resetSleeper();
      })
    })
  }

  onlock() {
    if(this.lock == true){
      $("#lock").prop("checked", false);
      this.lock = false;
    } else {
      $("#lock").prop("checked", true);
      this.lock = true;
    }
  }
  onlock1() {
    if(this.lock1 == true){
      $("#lock1").prop("checked", false);
      this.lock1 = false;
    } else {
      $("#lock1").prop("checked", true);
      this.lock1 = true;
    }
  }
  changeTemp(){
    if(this.temp == true){
      $(".celsius").removeClass("bolded");
      $(".fahrenheit").addClass("bolded");
      this.temp = false;
    } else {
      $(".fahrenheit").removeClass("bolded");
      $(".celsius").addClass("bolded");
      this.temp = true;
    }
  }
  
  
}
